.wrapper {
    display: flex;
    flex-direction: column;
    justify-Content: center;
    align-Items: center;

    .imagTeampet {
        margin: 2px;
        width: 250px;
        height: 200px;
        border: black 5px;
        border-radius: 10px;
        background-color: #7572FC;
        background-image: url(../../../icons/travel_app_person_purple1.png);
        background-size: cover
    }

    .loginOp {
        display: flex;
        flex-direction: column;
        align-items: center;

        .googlebuttonText {
            height: 38px;
            width: 260px;
            left: 0px;
            top: 0px;
            border-radius: 10.039999961853027px;
            display: inline-block;
            background: white;
            color: #444;
            border-radius: 10.04px;
            border: thin solid #888;
            box-shadow: 1px 1px 1px grey;
            white-space: nowrap;

        }

        .SignUpbtn {
            color: white;
            font-family: Poppins;
            font-style: normal;
            text-align: center;
            font-weight: 500;
            font-size: 18px;
            line-height: 27px;
            background-color: #FF8074;
            border: #FF8074;
            border-radius: 16px;
            height: 47.75px;
            width: 100px;

            &:hover {
                background-color: #fa6f63;

            }



        }

        .signupop {
            padding-top: 20px;
            display: flex;
            flex-direction: row;
        }

        .loginbtn {
            color: white;
            font-family: Poppins;
            font-style: normal;
            text-align: center;
            font-weight: 500;
            font-size: 18px;
            line-height: 27px;
            background-color: #FF8074;
            border: #FF8074;
            border-radius: 16px;
            height: 47.75px;
            width: 124px;

            &:hover {
                background-color: #fa6f63;

            }
        }

    }

    .other {
        display: flex;
        flex-direction: column;

        .inputs {
            padding: 2px;
            margin: 2px;
            height: 38px;
            width: 260px;
            border-radius: 10.037735939025879px;
        }
    }


}