.wrapper {
    background: #F9F9F9;
    

    //height: auto;
    .notlist {
        position: relative;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        .congratMsgt {
        
            font-family: 'Fira Sans', sans-serif;
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 125.5%;
            text-align: center;
            color: rgba(2, 2, 5, 0.8);

        }
        .signUpbtn {
            height: 47.75px;
            width: 257px;
            left: 0px;
            top: 0px;
            border-radius: 16px;
            background: #FF8074;
            border-radius: 16px;
        }
    }

    .listheader {
        //position: relative;
        //width: 360px;
        //height: 238px;
        left: 0px;
        top: 129px;

        .collapsible {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 2px;
            position: static;
            border-radius: 10px;
            flex: none;
            order: 0;
            flex-grow: 0;
            margin: 0px 0px;
            cursor: pointer;
            color: #ffffff;
            margin: 2px;
            .notlist{
                padding-top: 30px;
                background-color:  #F9F9F9;
            }

            .header {
                //width: 50vm;
                font-family: Poppins;
                font-style: normal;
                font-weight: normal;
                font-size: 20px;
                line-height: 30px;
                width: 90vw;
                background: #7572FC;
                border-radius: 10px;
                padding: 6px;

                &:hover {
                    background-color: #504df7;
                }
            }

            .content {
                // // padding: 20px;
                // // border-radius: 10px;
                // // background-color: #adaaf3;
                // &:hover {
                //    // background-color: #adaaf3;
                // }
                width: 80vw;

            }
        }
    }

   
}